.App {
  text-align: center;
}

.content {
  display: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)); /* 배경색을 흰색으로 설정 */
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.image-container {
  max-width: 100%;
  height: auto;
}



.header {
  background-color: darkblue;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.header h1 a {
  text-decoration: none;
  color: white;
  font-family: abster;
}

.nav ul {
  list-style: none;
  display: flex;
}

.nav ul li {
  margin: 0 10px;
}

.nav ul li a {
  text-decoration: none;
  color: white;
  transition: color 0.3s;
}

.nav ul li a:hover {
  color: #1fdfdf;
}

.hero {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.hero h2 {
  font-size: 60px;
  margin-bottom: 20px;
}

.hero p {
  font-size: 22px;
}

.image-container img {
  max-width: 100%;
}

/* 추가된 스타일 */
.text {
  display: none;
}

.text h3 {
   color: dark;
   top: 10px; /* 이미지 위에 나타나도록 마진 조정 */
   left: 10px; /* 위치 지정을 위해 position을 relative로 설정 */
   z-index: 1; 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
} 

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
